body, div, span, header, footer, nav, section, aside, article, ul, dl, dt, dd, li, a, p, h1, h2, h3, h4,h5, h6, i, b, textarea, button, input, select, figure, figcaption{    padding:0; margin:0; list-style:none; font-style:normal; text-decoration:none; border:none; font-weight:normal; box-sizing:border-box; -webkit-tap-highlight-color:transparent; -webkit-font-smoothing:antialiased;}
body{font-size:.2rem;}
input,
textarea{border:0; -webkit-appearance:none;}
html,body{height:100%; width:100%; background:#f2f2f2;}
a{text-decoration:none; color:#000;}

::-webkit-input-placeholder{color:#cecece;}
:-moz-placeholder{color:#cecece;}
::-moz-placeholder{color:#cecece;}
:-ms-input-placeholder{color:#cecece;}

.tip_layer{position:fixed; width:100%; height:100%; top:0; left:0; z-index:1100; background:rgba(0,0,0,.2);}
.tip_box{width:2.9rem; height:1.9rem; background:#fff; border-radius:.04rem; position:fixed; left:50%; top:50%; margin:-.95rem auto auto -1.45rem;}
.tip_box i{position:absolute; right:.07rem; top:.07rem; color:#d8d8d8; font-size:.22rem;}
.tip_box .tip_msg{padding:.65rem .1rem 0; text-align:center; line-height:.2rem; font-size:.15rem; color:#333;}
.tip_box .close_btn{background:#198ADB; border-radius:0 0 .04rem .04rem; width:100%; height:.5rem; line-height:.5rem; font-size:.14rem; text-align:center; color:#fff; position:absolute; bottom:0; left:0;} 

.next_page{margin:.1rem 0; padding:.1rem 0; font-size:.14rem; color:#999; text-align:center; position:relative;}
.next_page span{display:inline-block; padding:0 .1rem; background:#f2f2f2;}
.next_page:before{content:""; width:3.25rem; height:1px; background:#d8d8d8; position:absolute; top:50%; left:50%; margin-left:-1.64rem; z-index:-1;}

.no_data{position:fixed; width:100%; text-align:center; font-size:.14rem; color:#999; height:.2rem; line-height:.2rem; top:50%; margin-top:-.1rem; z-index:1000;}

/*animate*/
@keyframes slideInRight{
  	from{
    	transform:translate3d(100%, 0, 0);    
	}
	to{
	    transform:translate3d(0, 0, 0);
  	}
}

.slide-aside-enter-active{animation-name:slideInRight; animation-duration:.4s; opacity:1;}
.slide-aside-leave-active{transform:translate3d(100%, 0, 0); transition:all .3s ease-in; opacity:1;}
.slide-aside-enter,
.slide-aside-leave-to{opacity:0;}

.fade-in-enter-active,
.fade-in-leave-active{transition:opacity .5s; opacity:1;}
.fade-in-enter,
.fade-in-leave-to{opacity:0;}









