
.bqsqCon{width:100%; padding-bottom:.4rem;
}
.bqsqCon .headImg{display: inline-block; width:100%;
}
.bqsqCon .headImg img{width:100%;
}
.bqsqCon .writeBqsqCon{
}
.bqsqCon .writeBqsqCon h2{width:100%; text-align: center; font-weight: normal; font-size: .2rem; padding:.1rem 0 .14rem;
}
.bqsqCon .writeBqsqCon .writeCon{padding-left:.15rem; padding-right: .15rem
}
.bqsqCon .writeBqsqCon .writeCon li{background: #fff; border:.01rem solid #e6e6e6; line-height:.39rem; border-radius:0.02rem; margin-bottom: .14rem;
}
.bqsqCon .writeBqsqCon .writeCon li label{font-size:.14rem; display: inline-block; width: .72rem; padding-left: 0.2rem;
}
.bqsqCon .writeBqsqCon .writeCon li input[type=text]{width:2.3rem; height:.3rem; -webkit-box-sizing: none; box-sizing: none; font-size: .12rem; outline:none;
}
.bqsqCon .writeBqsqCon .writeCon li select{width:2.3rem; height:.3rem; font-size: .12rem; outline:none; background-color:transparent; border-color:transparent; -webkit-appearance: none;
}
.bqsqCon .writeBqsqCon .writeCon li select option{font-size: .12rem;
}
.bqsqCon .writeBqsqCon .writeCon li.otherCs{overflow: hidden;
}
.bqsqCon .writeBqsqCon .writeCon li textarea{width:2.3rem; height:1.48rem; font-size: .12rem; outline:none; float:left; padding: .10rem .08rem;
}
.bqsqCon .writeBqsqCon .writeCon li.otherCs label{float:left;
}
.bqsqCon .writeBqsqCon .writeCon li .submitInfoBtn{width:100%; background:#ff7f00; color:#fff; height:.4rem; font-size: .2rem
}
.bqsqCon .registTitle{text-align:center; margin:.05rem 0 .14rem;
}
.bqsqCon .registTitle img{width:1.76rem;
}
.bqsqCon .trademarkCon,
.bqsqCon .copyrightCon,
.bqsqCon .successCaseCon{padding-left:.15rem;
}
.bqsqCon .markConCss ul{overflow:hidden;
}
.bqsqCon .markConCss li{float:left; margin-right:.15rem; margin-bottom:.22rem;
}
.bqsqCon .markConCss li img{width:1.65rem;
}
.bqsqCon .markConCss .serverFee{background:#fff; font-size:.14rem; text-align:center; border:.01rem solid #d4d4d4; margin-top: -.05rem; font-size: .12rem; padding:.1rem 0 .08rem; color:#666;
}
.bqsqCon .markConCss .serverFee .projectName{font-size: .14rem; color:#333; margin-bottom: .04rem
}
.bqsqCon .successCase{margin-bottom:.14rem;
}
.bqsqCon .successCase .case1 img,
.bqsqCon .successCase .case2 img{width:3.45rem;
}
.bqsqCon .successCase .case3{margin-right:.14rem; float:left;
}
.bqsqCon .successCase .case3 img,
.bqsqCon .successCase .case4 img{width:1.65rem;
}
.bqsqCon .contact{background:#fff; border:1px solid #848484; text-align:center; line-height:.36rem; margin:.4rem .15rem 0 .15rem; font-size:.14rem; color:#404040; position:relative;
}
.bqsqCon .floatingWindow{position: fixed; top:0; left:0; width:100%; height:100%; background:rgba(0,0,0,.2); z-index: 99
}
.bqsqCon .floatingWindow .layerShap{position:relative; width:2.6rem; height:1.7rem; background:#fff; top:50%; left:50%; margin-left:-1.3rem; margin-top:-.85rem;
}
.bqsqCon .floatingWindow .layerShap .succesCon{border-top:.02rem solid #00a0e9;
}
.bqsqCon .floatingWindow .layerShap .succesCon .closeOpe{overflow:hidden;
}
.bqsqCon .floatingWindow .layerShap .succesCon .closeIcon{float:right; width:.2rem; margin:.07rem .07rem 0 0;
}
.bqsqCon .floatingWindow .tipCon{text-align:center; padding:0 .2rem; font-size: .14rem; color:#666;
}
.bqsqCon .floatingWindow .tipCon .successIcon{text-align:center;
}
.bqsqCon .floatingWindow .tipCon .successIcon img{width:.4rem;
}
.bqsqCon .floatingWindow .tipCon p{margin-top:.08rem;
}
.bqsqCon .floatingWindow .tipCon p.first{font-size:.18rem; color:#333;
}
.bqsqCon .floatingWindow .tipCon p.last{font-size: .12rem;
}
.tip_layer{position:fixed; width:100%; height:100%; top:0; left:0; z-index:1100; background:rgba(0,0,0,.2);
}
.tip_box{width:2.9rem; height:1.9rem; background:#fff; border-radius:.04rem; position:relative; left:50%; top:50%; margin:-.95rem auto auto -1.45rem;
}
.tip_box i{position:absolute; right:.07rem; top:.07rem; color:#d8d8d8; font-size:.22rem;
}
.tip_box .tip_msg{padding:.65rem .1rem 0; text-align:center; line-height:.2rem; font-size:.15rem; color:#333;
}
.tip_box .close_btn{background:#198ADB; border-radius:0 0 .04rem .04rem; width:100%; height:.5rem; line-height:.5rem; font-size:.14rem; text-align:center; color:#fff; position:absolute; bottom:0; left:0;
} 




